import React, { useEffect, useState } from 'react'
import BackendService from "../services/BackendService";
import BootstrapTable from 'react-bootstrap-table-next';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';
import paginationFactory from 'react-bootstrap-table2-paginator';
import { Link, useHistory } from 'react-router-dom';
import Skeleton, { SkeletonTheme } from 'react-loading-skeleton'
import 'react-loading-skeleton/dist/skeleton.css'


export default function Dashboard() {
  const history = useHistory();
  const [requests, setRequests] = useState([]);
  const [isFetching, setIsFetching] = useState(true);

  const navigate = (row) => {
    history.push(`/inspectionReport/${row.UnitId}/${row.RequestId}`);
  }

  const { SearchBar } = Search;
  const columns = [{
    dataField: 'PropertyType',
    text: 'Property Type',
    sort: false
    },{
      dataField: 'User_ID',
      text: 'User ID',
      sort: false
  },{
    dataField: 'Plot_Number',
    text: 'Plot Number',
    sort: false
  }, {
  dataField: 'Unit_Number',
  text: 'Unit Number',
  sort: false
}, {
  dataField: 'Floor_Number',
  text: 'Floor Number',
  sort: false
}, {
  dataField: 'Area_Name',
  text: 'Area Name',
  sort: false
}, {
  dataField: 'Street_Name',
  text: 'Street Name',
  sort: false
}, {
  dataField: 'Location',
  text: 'Location',
  sort: false
}, {
  dataField: 'FEWA_AccountNo',
  text: 'FEWA Account No',
  sort: false
}, {
  dataField: 'UnitPermitType',
  text: 'Unit Permit Type',
  sort: false
}, {
  dataField: 'InspectionAssignedDate',
  text: 'Inspection Assigned Date',
  sort: false
}, {
  dataField: 'ScheduleSiteVisitDate',
  text: 'Schedule Site Visit Date',
  sort: false
}, {
  dataField: 'SiteVisitedCompletedOn',
  text: 'Site Visited Completed On',
  sort: false
}, {
  dataField: 'SiteVisitedRemarks',
  text: 'Site Visited Remarks',
  sort: false
}, {
  dataField: 'SiteVisitedOn',
  text: 'Site Visited On',
  sort: false
},
{
  dataField: 'link',
  text: 'ACTION',
  formatter: (cell, row, rowIndex, formatExtraData) => {
      
    return (
        <button className="btn btn-primary"
            onClick={() => {
                navigate(row);
            }}
        >
        Report
        </button>
    )
  }}];
  
  const defaultSorted = [{
    dataField: 'name',
    order: 'desc'
  }];

  const pagination = paginationFactory({
    sizePerPage: 50,
    hideSizePerPage: true,
    prePageText: "Previous",
    nextPageText: "Next",
    withFirstAndLast: false,
    hidePageListOnlyOnePage: false
  });

  useEffect(() => {
    BackendService
      .getInspectedList()
      .then((response) => {
        setIsFetching(false);
        console.log(response.data.data.data);
        setRequests(JSON.parse(response.data.data.data));
        console.log(requests)
      });
  }, []);

    return (
        <div className="container-fluid page-body-wrapper">
        <div className="main-panel">
          <div className="content-wrapper">
            <div className="row">

              <div className="col-lg-12 grid-margin stretch-card">
                <div className="card">
                  <div className="card-body">
                    <h4 className="card-title">Inspected List </h4>
                    <div className="table-responsive">
                    
                    { isFetching ? (
                        <Skeleton duration={3} height={50} count={5} />
                    ) : (
                    <>
                    <BootstrapTable
                        bootstrap4
                        keyField="id"
                        data={ requests }
                        striped
                        condensed
                        columns={ columns }
                        defaultSorted={ defaultSorted }
                        pagination={ pagination } 
                      />
                    </>
                    ) }
                    </div>
                  </div>
                </div>
              </div>
            </div>
            
          </div>
        </div>
        </div>
    )
}
